.Toolbar {
    height: 56px;
    width: 100%;
    top: 0;
    left: 0;
    position: fixed;
    background-color: #02569C;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;
    box-shadow: 0 10px 6px -6px #999;
   


}

.Toolbar nav {
    height: 100%;
}
.Logo {
    height: 80%;
}


@media (max-width:499px){
    .DesktopOnly{
        display: none;
    }
}